import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-badge';

const Badge = ({ children, className, size, type, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${type}`]: Boolean(type),
      [`${CSS_PREFIX}--${size}`]: Boolean(size)
    }, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

Badge.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Size
   */
  size: oneOf([
    'small'
  ]),

  /**
   * Type
   */
  type: oneOf([
    'accent',
    'warning',
    'critical',
    'stable',
    'disabled',
    'status'
  ])
};

Badge.displayName = 'Badge';

export default Badge;
